.paperedge-bottom:after{
  content: '';
  display: block;
  width: 100%;
  transform: rotate(180deg);
  height: 65px;
  position: absolute;
  background-image: var(--background-image);
  margin-top: -35px;
  z-index:0;
  filter: drop-shadow( 5px 10px 5px rgba(0, 0, 0, .05));
}

.justify {
  text-align: justify;
  text-justify: inter-word;
}

.markdown > p {
  margin-top: 1rem;
  font-size: 1.05rem;
}

.markdown > h3 {
  margin-top: 2rem;
}

.markdown img {
  max-width: 100%;
  margin-top: 2rem;
  margin-bottom: 2rem;
  display: block;
  margin-left: auto;
  margin-right: auto;
  background-color: white;
  border-radius: 0.5rem;
}

.markdown img {
  box-shadow: 0 10px 20px rgba(0,0,0,0.05), 0 6px 6px rgba(0,0,0,0.01);
  transition: all 0.5s cubic-bezier(.25,.8,.25,1);
}

.markdown img:hover {
  box-shadow: 0 19px 38px rgba(0,0,0,0.15), 0 15px 12px rgba(0,0,0,0.05);
}

.reading-overlay {
  position: sticky;
  bottom: 0;
  left: 0;
  height: 25vh;
  width: 100%;
  background: linear-gradient(0deg, var(--paperwhite), rgba(252, 252, 244, 0));
}
